<template>
  <div class="slider_wrap">
    <div class="announce_slider">
      <van-swipe :autoplay="3000">
        <van-swipe-item v-for="(item, index) in sliderList" :key="index">
          <div @click="$router.push({
                                      path:'/announceDetail',
                                      query:{
                                        id: item.id
                                      }
                                   })">
            <div class="banner">
              <img v-lazy="getImgURL1(item.thumbnailUrl.pelativePath)">
            </div>
            <div class="bc">
              <img v-lazy="mask">
            </div>

            <div class="banner_info">
              <p>{{ item.title }}</p>
              <p>{{ timeStamp(item.publishTime) }}</p>
            </div>
          </div>
        </van-swipe-item>
      </van-swipe>
    </div>
  </div>
</template>

<script>
  import Dev from '../../../config/dev.env'
    export default {
      name:"slider",
      data(){
        return{
          mask: require('../../assets/imgs/announce/mask.png'),
          sliderList: []
        }
      },

      async created(){
        let that = this;
        let res = await this.$http.get(this.$HOST + '/open/health/message/center/app/messageSlip');
        let res1 = res.result.slipMessages;
        if(res.result.slipMessages){
          for(var i=0;i<res1.length;i++){
            if(res1[i].thumbnailUrl == null){
              res1[i].thumbnailUrl = '/static/imgs/error.png'
            }
          }
          that.sliderList = res1;

        }



        // if(res.result){
        //   that.sliderList = res.result.slipMessages;
        // }





       },
      methods: {
        getImgURL1(imgURL) {
          let IMG_ROOT = Dev.API_ROOT;
          IMG_ROOT = IMG_ROOT.replace('"', '');
          if(!imgURL){
            return  IMG_ROOT + '/file/' + imgURL;
          } else {
            return  IMG_ROOT + '/static/imgs/error.png';
          }
        }
      }
    }
</script>

<style scoped lang="scss">

  // 全局变量
  $position_absolute: absolute;
  $one_percent: 100%;
  $btnBgColor: #3ECE76;

  .slider_wrap{
    width: $one_percent;
    height: 460px;
    background: #F5F5F5;
    position: fixed;
    z-index: 9;

    .announce_slider{
      width: 93%;
      height: 400px;
      padding: 30px 0;
      position: fixed;
      top: 0;
      left: 25px;

      .banner, .bc{
        width: $one_percent;
        height: $one_percent;
      }

      .bc{
        position: $position_absolute;
        top: 0;
        left: 0;
      }

      .banner_info{
        position: $position_absolute;
        width: $one_percent;
        bottom: 50px;
        right: 20px;
        color: #FFF;
        text-align: right;

        p:first-child{
          font-size: 33px;
          padding-bottom: 10px;
        }

        p:last-child{
          font-size: 30px;
        }
      }
    }
  }
</style>



